.hardware-page {
	// padding: 30px 0
	@include media-breakpoint-down(md) {
		background: $white;
		.cart-step {
			margin: 0px -15px !important;
			border: none;
			&:before {
				@include media-breakpoint-down(sm) {
					display: none; } } } }
	.cart-title {
		@include pos(rel,null);
		@include media-breakpoint-down(sm) {
			flex-wrap: wrap;
			border-bottom: none; }
		.hardware-btn {
			@include pos(abs, -6 0 null null);
			@include media-breakpoint-down(md) {
				right: 15px; }
			@include media-breakpoint-down(sm) {
				@include pos(static,null);
				width: 100%; } } }
	.hardware-category {
		// max-width: 1000px
		// margin: 0 auto
		margin-bottom: 20px;
		.hardware-group {
			border-bottom: 1px dashed darken($white,10%);
			.group-title {
				@include pos(rel,null);
				@include fs(13rem);
				font-weight: 600;
				text-transform: uppercase;
				padding: 10px 0;
				@include media-breakpoint-down(sm) {
					@include fs(13rem);
					padding: 0px 0; }
				a {
					color: inherit;
					&:before {
						content: '';
						@include pos(abs, 0 null 0 0);
						right: 0; }
					@include media-breakpoint-down(sm) {
						display: block;
						padding: 10px 0;
						&:after {
							content: '\f105';
							font-family: 'fontawesome';
							margin-left: 5px;
							@include pos(abs, 10px 0 null null); } }
					&:hover {
						color: $blue; } }
				.btn-choose {
					border: 1px solid $blue;
					@include fs(12rem);
					font-weight: 500;
					color: $blue;
					padding: 0px 10px;
					line-height: 26px;
					display: inline-block;
					font-weight: normal;
					text-transform: none;
					@include bor-rad(50px);
					@include pos(abs, 7px 0 null null);
					@include media-breakpoint-down(sm) {
						display: none; }
					&:hover {
						color: $white;
						background: $blue; } } } } }
	.hardware-btn {
		padding: 10px 0;
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		flex-wrap: wrap;
		@include media-breakpoint-down(sm);
		a {
			display: block;
			text-align: center;
			line-height: 32px;
			@include bor-rad(50px);
			@include fs(13rem);
			font-weight: 500;
			padding: 0px 15px;
			color: $white;
			background: $blue;
			@include media-breakpoint-down(sm) {
				width: 100%; }
			&:before {
				content: '\f0c7';
				margin-right: 5px; }
			+ a {
				margin-left: 15px;
				@include media-breakpoint-down(sm) {
					margin-left: 0;
					margin-top: 10px; } }
			&:hover {
				color: $white;
				background: $main; } }
		.btn-excel {
			// background: $greengradient
			&:before {
				content: '\f0ce';
				margin-right: 5px; }
			&:hover {
 } }				// box-shadow: 0px 10px 20px -10px $green
		.btn-download {
			// background: $redgradient
			&:before {
				content: '\f019';
				margin-right: 5px; }
			&:hover {
 } }				// box-shadow: 0px 10px 20px -10px $red
		.btn-share {
			// background: linear-gradient(to right,#3b5998, #718dc8)
			&:before {
				content: '\f082';
				margin-right: 5px; }
			&:hover {
 } } }				// box-shadow: 0px 10px 20px -10px #3b5998
	.form-wrap {
		.frm-btn {
			width: 100%;
			text-align: center;
			padding-top: 10px; }
		.btn-order {
			width: auto;
			@include fs(14rem);
			font-weight: 500;
			text-transform: uppercase;
			background: $orangegradient;
			&:hover {
				background: $orangegradient;
				box-shadow: 0px 10px 20px -10px $orange; } } }
	.btn-loadmore {
		margin-bottom: 40px; } }
.modal-hardware {
	max-width: 1000px;
	@include media-breakpoint-down(lg) {
		max-width: 100%;
		margin: 0px 0; }
	.modal-content {
		@include media-breakpoint-down(lg) {
			min-height: 100vh;
			@include bor-rad(0); }
		.modal-body {
			@include media-breakpoint-down(lg) {
				max-height: calc(100vh - 48px);
				overflow: auto; }
			.searchbox {
				width: 100%;
				@include flexbox;
				@include bor-rad(5px);
				border: 1px solid darken($white,5%);
				margin-bottom: 10px;
				input {
					width: calc(100% - 40px);
					border: none;
					background: none;
					padding: 0px 15px; }
				.searchbutton {
					width: 40px;
					height: 40px;
					text-align: right;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center; } }
			.btn-showfilter {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include fs(14rem);
				font-weight: 500;
				background: darken($white,3%);
				padding: 10px 0;
				margin-bottom: 10px;
				display: none;
				@include media-breakpoint-down(md) {
					@include flexbox;
					margin-bottom: 0;
					width: 100%;
					background: $white; }
				.material-icons {
					@include fs(18rem);
					margin-right: 10px; } }
			.filter-wrap {
				&.option-group {
					background: darken($white,5%);
					box-shadow: none;
					@include media-breakpoint-down(md) {
						background: $white; } } }
			.hardware-list {
				@include flexbox;
				flex-wrap: wrap;
				border-top: 1px dashed darken($white,10%);
				border-left: 1px dashed darken($white,10%);
				@include media-breakpoint-down(md) {
					margin-bottom: 30px; }
				.hardware {
					padding: 10px;
					border-bottom: 1px dashed darken($white,10%);
					border-right: 1px dashed darken($white,10%);
					width: 50%;
					@include media-breakpoint-down(lg) {
						width: 50%; }
					@include media-breakpoint-down(sm) {
						width: 100%; }
					&:nth-child(2n) {
						border-left: none; }
					figure {
						@include flexbox;
						align-items: flex-start;
						align-content: flex-start;
						@include pos(rel,null);
						.img {
							width: 80px;
							height: 80px;
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							img {
								max-width: 100%;
								max-height: 100%; } }
						figcaption {
							width: calc(100% - 100px);
							padding: 0 0 0 10px;
							.name {
								@include fs(14rem);
								font-weight: 600;
								color: $blue;
								margin-bottom: 3px; }
							.price {
								@include flexbox;
								margin-bottom: 5px;
								.promotion {
									@include fs(10rem);
									fontw: 500;
									padding: 2px 5px;
									@include bor-rad(5px);
									&.off {
										@include pos(abs, 0px null null 0px);
										z-index: 3;
										background: lighten($red,10%);
										color: $white; } }
								.new {
									color: $main;
									@include fs(14rem);
									font-weight: 600;
									margin-right: 10px; }
								.old {
									@include fs(13rem);
									color: darken($white,40%);
									text-decoration: line-through; } }
							.btn-select {
								border: 1px solid $blue;
								@include bor-rad(50px);
								@include fs(12rem);
								font-weight: 500;
								padding: 0px 15px;
								line-height: 24px;
								display: inline-block;
								text-transform: none;
								color: $blue;
								&:before {
									content: '\f00c';
									margin-right: 5px; }
								&:hover {
									border-color: $main;
									background: $main;
									color: $white; } } } } } } } } }
.Module-365 {
	.cart-step {
		max-width: 1000px;
		margin: 0 auto 30px;
		@include media-breakpoint-down(md) {
			margin-bottom: 0; } } }
