.menu {
	// display: none
	width: 100%;
	@include media-breakpoint-down(lg) {
		@include pos(fix, 0 null 0 0);
		overflow: auto;
		z-index: 1000;
		background: $white;
		width: 330px;
		@include transform(translateX(-100%));
		@include transition(all,0.5s);
		&.open {
			@include transform(translateX(0)); } }
	@include media-breakpoint-down(sm) {
		width: 280px; }
	.menu-banner {
		display: none;
		@include media-breakpoint-down(lg) {
			display: block; } }
	.menu-login {
		display: none;
		@include media-breakpoint-down(lg) {
			@include flexbox;
			align-items: center;
			align-content: center;
			padding: 10px 20px;
			background: darken($white,5%);
			.avt, img {
				width: 40px;
				@include bor-rad(100px);
				border: 1px solid $white; }
			ul {
				padding-left: 10px;
				@include flexbox;
				flex-wrap: wrap;
				li {
					display: block;
					width: 100%;
					&:nth-child(1) {
						order: 2; }
					&:nth-child(2) {
						order: 1; } }
				a {
					display: block;
					@include fs(11rem);
					line-height: 1.5;
					color: darken($white,60%);
					// opacity: 0.8
					text-transform: uppercase;
					&:after {
						content: '\f105';
						margin-left: 5px; } }
				.btn-login {
					@include fs(13rem);
					font-weight: 700;
					opacity: 1; } } } }
	.menu-wrap {
		@include media-breakpoint-down(lg) {
			padding: 10px 0; } }
	ul.menu-link {
		@include flexbox;
		align-items: center;
		align-content: center;
		&.menu-pc {
			padding: 0 0 5px;
			@include pos(rel,null);
			@include media-breakpoint-down(lg) {
				display: none; } }
		&.menu-mobile {
			display: none;
			@include media-breakpoint-down(lg) {
				@include flexbox;
				flex-wrap: wrap;
				align-items: center;
				align-content: center;
				flex-direction: column; } }
		> li {
			flex: auto;
			@include media-breakpoint-down(lg) {
				width: 100%;
				padding: 0; }
			> a {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				line-height: 20px;
				@include fs(14rem);
				padding: 10px 20px;
				color: $main;
				@include pos(rel,null);
				@include media-breakpoint-up(xl) {
					&:before, &:after {
						content: '';
						width: 1px;
						height: 8px;
						background: $blue;
						@include pos(abs, null null null 50%);
						opacity: 0; }
					&:before {
						top: 0px; }
					&:after {
						bottom: 0px; } }
				@include media-breakpoint-down(lg) {
					color: darken($white,60%);
					padding: 7px 15px;
					justify-content: flex-start;
					@include fs(14rem); }
				.material-icons, > img {
					margin-right: 5px;
					@include fs(18rem);
					@include media-breakpoint-up(xl) {
						display: none; }
					@include media-breakpoint-down(lg) {
						@include fs(24rem);
						margin-right: 10px; } }
				span {
					font-weight: 700;
					@include media-breakpoint-up(xl) {
						text-transform: uppercase;
						font-weight: 500; }
					@include media-breakpoint-down(lg) {
						font-weight: normal; } } }
			@mixin menuactive($color,$bg) {
				@include media-breakpoint-up(xl) {
					&:hover, &.active {
						> a {
							color: $color;
							&:before, &:after {
								opacity: 1; } } } } }
			@include menuactive($blue,$blue);
			&.has-sub {
				.sub1, .sub2 {
					@include pos(abs, 85% 0 null 0);
					z-index: 100;
					background: $white;
					box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.15);
					border-top: 2px solid $blue;
					@include bor-rad(5px);
					padding: 25px;
					visibility: hidden;
					opacity: 0;
					margin-top: 20px;
					@include transition(all,0.3s);
					.sub-img {
						margin-bottom: 10px; }
					.sub-title {
						@include fs(16rem);
						font-weight: 500;
						border-bottom: 1px solid darken($white,5%);
						padding-bottom: 5px;
						@include pos(rel,null);
						margin-bottom: 15px;
						&:before {
							content: '';
							display: block;
							width: 50px;
							height: 2px;
							background: $blue;
							@include pos(abs, null null -1px 0);
							z-index: 1; } }
					.sub-link {
						a {
							display: block;
							color: darken($white,50%);
							@include fs(14rem);
							padding: 8px 0;
							&:before {
								content: '\f178';
								display: inline-block;
								vertical-align: top;
								width: 0px;
								@include transition(all,0.3s);
								overflow: hidden; }
							&:hover {
								color: $blue;
								&:before {
									width: 20px; } } } } }
				&:hover {
					@include media-breakpoint-up(xl) {
						.sub1, .sub2 {
							visibility: visible;
							opacity: 1;
							margin-top: 0; } } } } } }
	.menu-contact {
		display: none;
		margin: 0px 20px;
		border-top: 1px solid darken($white,5%);
		padding: 20px 0;
		@include media-breakpoint-down(lg) {
			display: block; }
		.company {
			@include fs(14rem);
			font-weight: 700;
			text-transform: uppercase;
			margin-bottom: 10px;
			color: $blue; }
		.contact-info {
			li {
				padding-left: 25px;
				@include pos(rel,null);
				@include fs(13rem);
				line-height: 1.4;
				color: darken($white,60%);
				margin-bottom: 5px;
				.material-icons {
					@include pos(abs, 1px null null 0);
					@include fs(18rem); } } } } }
