.home-title, .page-title {
	@include fs(20rem);
	line-height: 1.4;
	font-weight: 700;
	text-transform: capitalize;
	padding: 0 0 10px;
	@include pos(rel,null);
	// border-bottom: 1px solid darken($white,5%)
	&:after {
		content: '';
		display: block;
		width: 50px;
		height: 2px;
		background: $blue;
		@include pos(abs, null null -1px 0);
		z-index: 3; }
	@include media-breakpoint-down(lg) {
		@include fs(18rem); }
	@include media-breakpoint-down(sm) {
		@include fs(16rem); } }
.page-title {
	&.text-center {
		&:after {
			display: none; } } }
.home-video {
	margin: 30px 0 20px;
	// height: calc(100% - 50px)
	@include bor-rad(5px);
	overflow: hidden;
	@include pos(rel,null);
	background: $white;
	border: 2px solid darken($white,10%);
	padding: 20px;
	@include media-breakpoint-down(lg) {
		padding: 15px; }
	@include media-breakpoint-down(md) {
		margin: 0 0 20px;
		height: auto; }
	.home-title {
		margin-bottom: 20px;
		@include media-breakpoint-down(md) {
			margin-bottom: 15px; } }
	.video-wrap {
		margin: 0px -20px;
		overflow: hidden;
		.video-des {
			color: $main;
			.video-name {
				@include fs(13rem);
				font-weight: 500;
				@include textover(2); }
			.video-view {
				@include fs(12rem);
				opacity: 0.8;
				font-weight: 300; } }
		.video-big {
			.video-des {
				padding: 15px;
				background: $mainblue; } }
		.video-list {
			background: $white;
			height: 377px;
			overflow: auto;
			padding: 0px 20px;
			&::-webkit-scrollbar-track {
				background-color: transparent;
				padding: 2px; }
			&::-webkit-scrollbar {
				width: 5px;
				background-color: transparent; }
			&::-webkit-scrollbar-thumb {
				background-color: color-rgba($black,0.3); }
			@include media-breakpoint-down(lg) {
				height: 297px; }
			@include media-breakpoint-down(md) {
				height: 250px; }
			.video {
				display: block;
				@include transition(all,0.5s);
				@include pos(rel,null);
				+ .video {
					margin-top: 15px;
					@include media-breakpoint-down(md) {
						margin-top: 10px; } }
				&:before {
					content: '';
					@include pos(abs, 0 null 0 0);
					width: 3px;
					background: $bluegradient-v;
					opacity: 0;
					@include transition(all,0.5s); }
				figure {
					@include flexbox;
					align-items: flex-start;
					align-content: flex-start;
					.video-img {
						width: 90px;
						@include pos(rel,null);
						&:before {
							content: '\f04b';
							background: $white;
							color: $blue;
							@include fs(12rem);
							width: 32px;
							height: 32px;
							@include bor-rad(100px);
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							@include pos(abs,50% null null 50%);
							@include transform(translate(-50%,-50%));
							@include transition(all,0.5s); } }
					figcaption {
						width: calc(100% - 90px);
						padding-left: 10px; } }
				&:hover {
					figure {
						.video-img {
							&:before {
								background: $blue;
								color: $white; } }
						figcaption {
							.video-name {
								color: $blue; } } } } } } } }
.btn-viewall {
	@include pos(abs, 23px 20px null null);
	@include fs(12rem);
	text-transform: uppercase;
	color: darken($white,50%);
	@include flexbox;
	align-items: center;
	align-content: center;
	@include media-breakpoint-down(lg) {
		top: 20px; }
	@include media-breakpoint-down(sm) {
		top: 16px; }
	&:hover {
		color: $blue; } }

.home-qc, .home-qc2 {
	margin-bottom: 20px;
	@include media-breakpoint-down(md) {
		margin-bottom: 15px; }
	@include media-breakpoint-down(sm) {
 }		// display: none
	.qc-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -10px;
		@include media-breakpoint-down(md) {
			display: block;
			white-space: nowrap;
			overflow: auto;
			overflow-y: hidden;
			margin: 0px -15px;
			padding: 0px 15px; } }
	.item {
		width: 25%;
		padding: 0px 10px;
		@include media-breakpoint-down(md) {
			display: inline-block;
			vertical-align: top;
			width: 280px;
			padding: 0px;
			+ .item {
				margin-left: 10px; } }
		@include media-breakpoint-down(sm) {
			width: 180px;
			display: inline-block;
			vertical-align: top; } }
	.banner-qc {
		display: block;
		@include bor-rad(5px);
		overflow: hidden;
		margin-bottom: 0px;
		&:hover {
			filter: brightness(120%);
 } } }			// box-shadow: 0px 5px 20px color-rgba($black,0.3)
.home-qc2 {
	margin-bottom: 30px;
	@include media-breakpoint-down(lg) {
		margin-bottom: 15px; }
	.qc-list {
		@include media-breakpoint-down(md) {
			margin: 0px -10px; }
		@include media-breakpoint-down(sm) {
			margin: 0;
			padding: 0; }
		.item {
			width: 50%;
			padding: 0px 10px;
			@include media-breakpoint-down(md) {
				width: 50%; }
			@include media-breakpoint-down(sm) {
				width: 100%;
				display: block;
				padding: 0;
				+ .item {
					margin-left: 0;
					margin-top: 10px; } } } } }

.home-category {
	@include pos(rel,null);
	background: $white;
	border: 2px solid $blue;
	@include bor-rad(5px);
	padding: 5px;
	margin-bottom: 20px;
	@include media-breakpoint-down(md) {
		border: none;
		background: none;
		padding: 0;
		margin-bottom: 15px; }
	.home-title {
		margin-bottom: 20px;
		display: none; }
	.btn-viewall {
		display: none; }
	.category-wrap {}
	.category-list {
		@include flexbox;
		flex-wrap: wrap;
		@include media-breakpoint-down(md) {
			display: block;
			overflow: auto;
			white-space: nowrap; }
		@include media-breakpoint-down(sm) {
			padding: 0px 15px;
			margin: 0px -15px; }
		.item {
			width: calc(100% / 8);
			@include media-breakpoint-down(md) {
				// width: 170px
				display: inline-block;
				vertical-align: top;
				white-space: nowrap;
				width: 170px;
				+ .item {
					margin-left: 5px; } }
			@include media-breakpoint-down(sm) {
				width: 150px; }
			.category-item {
				@include flexbox;
				align-items: center;
				align-content: center;
				padding: 0px 10px;
				height: 70px;
				@include bor-rad(5px);
				@include media-breakpoint-down(md) {
					background: darken($white,10%);
					height: 60px;
 }					// border: 2px solid darken($white,10)
				.img {
					img {
						width: 100%; } }
				.name {
					@include flexbox;
					align-items: center;
					align-content: center;
					.material-icons {
						color: $blue;
						@include flexbox;
						width: 32px;
						height: 32px;
						align-items: center;
						align-content: center;
						justify-content: center; }
					span {
						flex: 1;
						padding-left: 5px;
						@include fs(13rem);
						font-weight: 500;
						color: $main;
						line-height: 1.2;
						white-space: normal;
						@include media-breakpoint-down(lg) {
							@include fs(13rem);
							font-weight: normal; }
						@include media-breakpoint-down(sm) {
							@include fs(13rem);
							font-weight: 700; } } }
				&:hover {
					background: $main;
					border-color: $main;
					.name {
						span {
							color: $white; } } } } } } }

.home-hotsale {
	@include pos(rel,null);
	margin-bottom: 30px;
	@include media-breakpoint-down(md) {
		margin-bottom: 15px; }
	.tab-content {
		background: darken($white,10%);
		overflow: hidden;
		margin-bottom: 15px;
		@include bor-rad(5px);
		@include media-breakpoint-down(sm) {
			@include bor-rad(0);
			margin: 0 -15px 15px;
			padding: 0;
			box-shadow: none;
			overflow: visible;
			background: none; } }
	.btn-wrap {
		text-align: center; }
	.nav-tabs {
		margin-bottom: 20px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 15px; } }
	.btn-viewall {
		@include pos(static, null);
		@include fs(14rem);
		// font-weight: 500
		text-transform: none;
		color: $white;
		background: $main;
		@include bor-rad(50px);
		line-height: 36px;
		display: inline-block;
		padding: 0px 30px;
		@include media-breakpoint-down(md) {
			padding: 0px 20px;
			@include fs(13rem);
			line-height: 36px; }
		&:before {
			content: '\f055';
			font-family: 'fontawesome';
			margin-right: 10px; }
		&:hover {
			background: $blue; }
		&:after {
			display: none; } }
	.product-slick {
		padding: 0 15px;
		@include bor-rad(5px);
		@include media-breakpoint-down(sm) {
			overflow: auto;
			white-space: nowrap;
			padding: 0 15px; }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 1;
			@include fs(0);
			// color: $white
			text-shadow: 0px 1px 5px color-rgba($black,0.1);
			opacity: 0;
			@include transition(all,0.5s);
			&:before {
				content: '';
				@include fs(14rem);
				@include flexbox;
				width: 36px;
				height: 36px;
				justify-content: center;
				align-items: center;
				@include bor-rad(50px);
				background: $white;
				border: 1px solid darken($white,5%); }
			&:hover {
				&:before {
					background: $blue;
					color: $white;
					border-color: $blue; } } }
		.slick-prev {
			left: 0px;
			&:before {
				content: '\f177'; } }
		.slick-next {
			right: 0px;
			&:before {
				content: '\f178'; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; }
			.slick-prev {
				left: 10px; }
			.slick-next {
				right: 10px; } }
		.slick-track {
			padding: 15px 0; }
		.slick-list {
			margin: 0px -5px; }
		.item {
			height: 100%;
			padding: 0px 5px;
			@include media-breakpoint-down(sm) {
				display: inline-block;
				vertical-align: top;
				width: auto;
				width: 150px;
				padding: 0;
				+ .item {
					margin-left: 10px; } } } }
	.product-col {
		height: 100%;
		white-space: normal;
		@include media-breakpoint-down(sm) {
			border: 1px solid darken($white,5%); }
		figure {
			.product-img {
				height: 150px; }
			figcaption {
				.product-price {
					min-height: 30px; } } } } }

.home-brand {
