		display: inline-block;
		padding: 0px 30px;
		@include media-breakpoint-down(md) {
			margin: 15px 0 0;
			padding: 0px 20px;
			@include fs(13rem);
			line-height: 36px; }
		&:before {
			content: '\f055';
			font-family: 'fontawesome';
			margin-right: 10px; }
		&:hover {
			background: $blue; }
		&:after {
			display: none; } }
	.facebook-wrap {
		.fb-list {
			@include flexbox;
			flex-wrap: wrap;
			margin: 0px -10px 20px;
			@include media-breakpoint-down(lg) {
				margin: 0px -5px 20px; }
			@include media-breakpoint-down(md) {
				display: block;
				white-space: nowrap;
				overflow: auto;
				margin: 0px; }
			.item {
				padding: 0px 10px;
				width: calc(100% / 5);
				@include media-breakpoint-down(lg) {
					padding: 0px 5px; }
				@include media-breakpoint-down(md) {
					display: inline-block;
					vertical-align: top;
					width: 180px;
					padding: 0px;
					+ .item {
						margin-left: 10px; } } }
			.fb-item {
				display: block;
				height: calc(100% - 0px);
				@include bor-rad(5px);
				overflow: hidden;
				background: $white;
				@include media-breakpoint-down(md) {
					margin-bottom: 0; }
				figure {
					.fb-img {
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						height: 150px;
						overflow: hidden;
						@include pos(rel,null);
						@include media-breakpoint-down(lg) {
							height: 120px; }
						@include media-breakpoint-down(md);
						&:before {
							content: '\f09a';
							background: $white;
							@include fs(16rem);
							width: 32px;
							height: 32px;
							@include bor-rad(100px);
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							color: $facebook;
							@include pos(abs, null null 10px 10px);
							box-shadow: 0px 3px 5px color-rgba($black,0.1); }
						img {
							min-width: 100%;
							min-height: 100%;
							@include media-breakpoint-down(md) {
								object-fit: cover; } } }
					figcaption {
						padding: 15px;
						@include media-breakpoint-down(md) {
							padding: 10px; }
						time {
							display: inline-block;
							@include fs(13rem);
							color: darken($white,40%);
							margin-bottom: 5px;
							@include media-breakpoint-down(lg) {
								@include fs(12rem); } }
						.fb-des {
							@include fs(14rem);
							line-height: 1.4;
							color: $main;
							display: -webkit-box;
							-webkit-line-clamp: 3;
							-webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							@include transition(all,0.5s);
							height: (14/16)*3*1.4rem;
							@include media-breakpoint-down(md) {
								@include fs(13rem);
								white-space: normal;
								min-height: 56px;
								height: (13/16)*3*1.4rem; } } } }
				&:hover {
					figure {
						figcaption {
							.fb-des {
								color: $blue; } } } } } } } }
.homepage {
	.pages {
		display: none!important; } }
